import React from 'react';
import { motion } from 'framer-motion';
import Button from '../components/Button';
import Modal from '../components/Modal';
import SEO from '../components/SEO';
import Testimonials from '../components/Testimonials';
import { useTheme } from '../contexts/ThemeContext';
import { colors, typography } from '../styles/sharedStyles';

function Home() {
  const { darkMode } = useTheme();
  const [selectedService, setSelectedService] = React.useState(null);

  const services = [
    {
      title: "Derecho Tecnológico y Fintech",
      icon: "💻",
      description: "Asesoramiento en regulaciones tecnológicas y finanzas digitales.",
      details: "Nuestro equipo especializado en derecho tecnológico y fintech ofrece asesoramiento integral en temas como protección de datos, regulación de criptomonedas, contratos inteligentes y cumplimiento normativo en el sector fintech."
    },
    {
      title: "Derecho Ambiental y Energías Renovables",
      icon: "🌿",
      description: "Soluciones legales para proyectos sostenibles y energías limpias.",
      details: "Brindamos asesoría legal especializada en proyectos de energías renovables, gestión ambiental, permisos y licencias ambientales, así como en la implementación de políticas de sostenibilidad corporativa."
    },
    {
      title: "Propiedad Intelectual en E-commerce",
      icon: "🛡️",
      description: "Protección de marcas y derechos de autor en el comercio electrónico.",
      details: "Ofrecemos servicios de protección de propiedad intelectual en el ámbito digital, incluyendo registro de marcas en línea, protección de derechos de autor en plataformas digitales y resolución de disputas de nombres de dominio."
    },
  ];

  return (
    <>
      <SEO 
        title="Inicio - Bufete Salas Granados"
        description="Bufete Salas Granados ofrece asesoría legal estratégica para el éxito empresarial en Costa Rica. Especialistas en derecho corporativo, tecnológico y ambiental."
        keywords="abogados costa rica, asesoría legal, derecho corporativo, derecho tecnológico, derecho ambiental"
      />
      <div className={`${colors.background} ${typography.sans} ${darkMode ? 'dark' : ''}`}>
        <section className={`${colors.primary} text-white py-20`}>
          <div className="container mx-auto px-4">
            <motion.h1 
              className="text-4xl md:text-5xl font-bold mb-6"
              initial={{ opacity: 0, y: -20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.5 }}
            >
              Bufete Salas Granados
            </motion.h1>
            <motion.p 
              className="text-xl mb-8"
              initial={{ opacity: 0, y: -20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.5, delay: 0.2 }}
            >
              Asesoría legal estratégica para el éxito empresarial en Costa Rica
            </motion.p>
            <Button to="/contacto" variant="secondary">Solicitar Consulta</Button>
          </div>
        </section>

        <section className="py-16">
          <div className="container mx-auto px-4">
            <h2 className="text-3xl font-bold mb-12 text-center">Áreas de Práctica Especializadas</h2>
            <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
              {services.map((service, index) => (
                <motion.div 
                  key={index}
                  className="bg-white rounded-lg shadow-lg p-6 text-center cursor-pointer"
                  whileHover={{ scale: 1.05 }}
                  whileTap={{ scale: 0.95 }}
                  onClick={() => setSelectedService(service)}
                >
                  <div className="text-4xl mb-4" aria-hidden="true">{service.icon}</div>
                  <h3 className="text-xl font-semibold mb-2">{service.title}</h3>
                  <p className={colors.text}>{service.description}</p>
                </motion.div>
              ))}
            </div>
          </div>
        </section>

        <section className={`${colors.secondary} text-white py-16`}>
          <div className="container mx-auto px-4">
            <h2 className="text-3xl font-bold mb-8 text-center">Nuestro Enfoque</h2>
            <ul className="grid grid-cols-1 md:grid-cols-2 gap-8">
              {[
                "Análisis legal predictivo con tecnología de vanguardia",
                "Estrategias de resolución alternativa de conflictos",
                "Asesoría en cumplimiento normativo internacional",
                "Enfoque en sostenibilidad legal y responsabilidad social",
              ].map((item, index) => (
                <li key={index} className="flex items-center bg-white text-gray-800 rounded-lg shadow p-4">
                  <span className="text-blue-500 mr-4" aria-hidden="true">✔️</span>
                  <span>{item}</span>
                </li>
              ))}
            </ul>
          </div>
        </section>

        <Testimonials />

        <section className="py-16">
          <div className="container mx-auto px-4 text-center">
            <h2 className="text-3xl font-bold mb-8">¿Listo para impulsar su negocio?</h2>
            <p className="text-xl mb-8">Agende una consulta estratégica y descubra cómo podemos ayudarle.</p>
            <Button to="/contacto" variant="primary">Agendar Consulta</Button>
          </div>
        </section>

        <Modal
          isOpen={!!selectedService}
          onClose={() => setSelectedService(null)}
          title={selectedService?.title}
        >
          <p>{selectedService?.details}</p>
          <div className="mt-4">
            <Button to="/servicios" variant="primary">Ver todos los servicios</Button>
          </div>
        </Modal>
      </div>
    </>
  );
}

export default Home;